}

.section-info .divider {
  content: "";
  margin: 5em auto;
  display: flex;
  justify-content: center;
  width: 70%;
  height: 3px;
  background-color: $color-grey-6;
  max-width: 30em;
  opacity: 0.3;
}

.sec3 {
  display: none;
  transform: translateY(0) scale(1);
  background-color: $light-bg-color;
}

.sec4 {
  display: none;
  transform: translateY(0) scale(1);
  background-color: $light-bg-color;
  .section-info {
    padding-top: 6em;
    .approach-steps {
      display: flex;
      width: 100%;
      justify-content: center;
      padding-top: 5em;
      i {
        align-self: center;
        font-size: xxx-large;
        color: $highlight-color;
      }
    }
    .approach-step {
      padding: 0.2em 2em;
      margin: 0em 1em;
      h1 {
        margin-bottom: 0.2em;
        color: $highlight-color;
      }
    }
    .approach-descriptions {
      max-width: 50em;
      margin: 0 auto;
      margin-bottom: 5em;
    }
    .approach-desc {
      display: flex;
      margin-bottom: 2em;
      padding: 1em;
      transition: $transition-default;
      border-radius: 5px;
      outline: 1px solid $dark-bg-color;
      &:hover {
        // test
        outline: 1px solid $highlight-color;
      }
      .steps-arrow {
        width: 3em;
        h1 {
          width: 2em;
          height: 2em;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          background-color: $color-grey-1;
          margin: 0 auto;
          color: $dark-bg-color;
          margin-bottom: 1em;
        }
        i {
          color: $highlight-color;
          font-size: 3em;
          height: 100%;
        }
      }
      .desc {
        text-align: left;
        margin-left: 1.5em;
        padding: 0.5em 1em 1em 1em;
        h1 {
          color: $highlight-color;
        }
      }
